import React from 'react'
import {Avatar, Button, Card, Icon, message, Spin} from 'antd'
import './index.less'
import axios from "axios";
import {baseUrl} from "../../config";
import moment from "moment";
import {Link} from "react-router-dom";
import Zmage from 'react-zmage'

export default class Work extends React.Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    componentDidMount() {
        // 从缓存获取所有works
        const ws = this.getWorks();
        // 筛选该条work
        let w = this.filter(ws);
        // 获取workers
        this.getWorker();
        this.setState({
            work: w,
        });
        console.log(this.state)
    }

    getWorks = () => {
        // 有无缓存？返回：请求->缓存->返回
        const cached = localStorage.works_by_me !== undefined;
        if (cached) {
            // 返回数据
            this.setState({
                myFavs: JSON.parse(localStorage.works_by_me),
            });
            return JSON.parse(localStorage.works_by_me)
        } else {
            // 请求
            axios.get(
                baseUrl + '/api/get_works_by_me',
                {
                    params: {
                        token: localStorage.customer_token,
                    }
                })
                .then((Response) => {
                    console.log('请求了works_by_me');
                    // 设置缓存
                    localStorage.works_by_me = JSON.stringify(Response.data.works);
                    return Response.data.works
                });
        }
    };
    filter = (ws) => {
        // const ws = this.state.myFavs;
        const id = this.props.match.params.id;
        for (const w in  ws) {
            const p = ws[w];
            if (p.id == id) {
                console.log('avl', p);
                return p
            }
        }
    };
    getWorker = () => {
        axios.get(
            baseUrl + '/api/get_my_workers',
            {
                params: {
                    token: localStorage.customer_token,
                    id_work: this.props.match.params.id,
                }
            })
            .then((Response) => {
                // 设置缓存
                console.log('workers', Response.data.workers);
                this.setState({
                    myWorkers: Response.data.workers,
                })
            });
    };
    renderMyWorker = () => {
        this.state.work.workers.map(() => {

        })
    };
    type_convert = (type) => {
        switch (type) {
            case 1:
                return "代办类";
            case 2:
                return "转发类";
            case 3:
                return "问卷类";
            case 0:
                return "其他";
            default:
                return "全部";
        }
    };
    pass = (id_worker) => {
        axios.get(
            baseUrl + '/api/set_worker_status',
            {
                params: {
                    token: localStorage.customer_token,
                    sure: 0,    //['已审核', '已提交', '未提交', 5 => '已领取', -1 => '未通过', -2 => '客服中']
                    id_work: this.state.work.id,
                    id_worker: id_worker
                }
            })
            .then((Response) => {
                const d = Response.data;
                console.log('返回数据', Response.data);
                if (d.sorry !== undefined || d.error !== undefined) {
                    message.error(d.sorry || d.error);
                } else {
                    // 刷新缓存
                    message.success('已审核通过！');
                }
            })
    };
    passNot = (id_worker) => {
        axios.get(
            baseUrl + '/api/set_worker_status',
            {
                params: {
                    token: localStorage.customer_token,
                    sure: -1,    //['已审核', '已提交', '未提交', 5 => '已领取', -1 => '未通过', -2 => '客服中']
                    id_work: this.state.work.id,
                    id_worker: id_worker
                }
            })
            .then((Response) => {
                const d = Response.data;
                console.log('返回数据', Response.data);
                if (d.sorry !== undefined || d.error !== undefined) {
                    message.error(d.sorry || d.error);
                } else {
                    // 刷新缓存
                    message.success('操作成功');
                }
            })
    };
    renderWorkCell = () => {
        const p = this.state.work;
        return (
            <div>
                <div className='back-to-last'>
                    <Link to={`/about/index/TaskPosted`}>
                        <Icon type="left"/>
                        返回
                    </Link>
                </div>
                <div className='main-body-work'>
                    <div className='work-title'>
                        {p.work_name}
                    </div>
                    <div>
                        {p.work_content}
                    </div>
                    <div className='work-money'>
                        报酬:{p.work_money !== 0 ? p.work_money : '可以协商'}{p.work_tpl === 2 || p.work_tpl === 3 ? '积分' : '元'}/人
                    </div>
                    <div>
                        人数:{p.work_person_number}

                    </div
                    >
                    <div>
                        截止时间:{
                        moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?
                            '今天内'
                            : moment.unix(p.stop_at).format("MM-DD HH:mm")
                    }
                    </div>
                    <div>

                        发布时间:{moment.unix(p.created_at).format("MM-DD HH:mm")}
                    </div>
                    {
                        this.state.myWorkers !== undefined
                            ?
                            <div className='my-workers'>
                                审核列表
                                {this.state.myWorkers.map(w => {
                                    return (
                                        <Card
                                            title={
                                                <div className='avatar-name'>
                                                <Avatar size={40} shape='square'
                                                        src={`${baseUrl}/${w.worker_avatar_url}`}
                                                        className='avatar'>头像</Avatar>
                                                <span className='name'>{w.worker_name}</span>
                                            </div>
                                            }
                                        >
                                            <div className='my-workers-cell'>
                                                {w.datas.map(m => {
                                                return (
                                                        <Zmage src={`${baseUrl}/${m.content}`} width='33%'/>
                                                )
                                            })}

                                                <div className='accept-time'>接单时间:{moment.unix(w.created_at).format("MM-DD HH:mm")}</div>
                                                <Button type='primary' onClick={this.pass.bind(this, w.id)}>审核通过</Button>
                                                <Button type='danger' onClick={this.passNot.bind(this, w.id)}>审核不通过</Button>
                                            </div>
                                        </Card>

                                    )
                                })}
                            </div>
                            : null
                    }
                </div>
            </div>
        )
    };

    render() {
        return (
            <div className='work'>
                {
                    this.state.work === undefined
                        ? <Spin/>
                        : this.renderWorkCell()
                }
            </div>
        )
    }
}