import React from 'react'
import {Button, Icon, Modal} from 'antd'
import {Link} from 'react-router-dom'
import './index.less'


export default class Frap extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visiblePostLinks: false,
        }
    }

    showPostLinks = () => {
        this.setState({
            visiblePostLinks: true,
        });
    };
    handleOk = e => {
        console.log(e);
        this.setState({
            visiblePostLinks: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visiblePostLinks: false,
        });
    };

    render() {
        return (
            <div>
                {/* 发布任务 */}
                <div className='frap' onClick={this.showPostLinks}>
                    <Button>
                        {/*<Icon type="plus-circle" style={{fontSize: '30px'}}/>*/}
                        <Icon type="plus"/>
                        发布任务
                    </Button>
                </div>
                <Modal
                    visible={this.state.visiblePostLinks}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    style={{textAlign: 'center'}}
                >
                    <Link to='/admin/customer/upload/question/1'>
                        <div className='light-blue'>
                            <h2>代办类</h2>
                            <p>代拿快递、代充电费等</p>
                        </div>
                    </Link>
                    <br/>
                    <br/>
                    <Link to='/admin/customer/upload/question/2'>
                        <div className='light-red'>
                            <h2>转发类</h2>
                            <p>转发问卷、海报、链接</p>
                        </div>
                    </Link>
                    <br/>
                    <br/>
                    <Link to='/admin/customer/upload/question/3'>
                        <div className='light-yellow'>
                            <h2>问卷类</h2>
                            <p>帮忙填写问卷</p>
                        </div>
                    </Link>
                    <br/>
                    <br/>
                    <Link to='/admin/customer/upload/question/0'>
                        <div className='light-green'>
                            <h2>
                                其他
                            </h2>
                        </div>
                    </Link>
                </Modal>
            </div>
        )
    }
}