import React from 'react'
import axios from "axios";
import {baseUrl} from "../../../../config";
import InfiniteScroll from "react-infinite-scroller";
import {Avatar, Button, message, Modal} from "antd";
import moment from 'moment'

import './index.less'
import Frap from "../../../../cpns/Frap";
import {Link} from "react-router-dom";

export default class SortByPost extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            works: [],
            hasMoreItems: true,
            // 审核modal
            visibleSubmitShit: false,
            //分组查询
            groupBy: 'all',
            idActive: 0,
            visibleCommunications: false,
            tpl: props.tpl,
        }
    }

    componentDidMount() {
        console.log('pppp', this.props)

    }

    working = (id) => {
        console.log(id);
        axios.get(
            baseUrl + '/api/enroll',
            {
                params: {
                    token: localStorage.customer_token,
                    id_work: id,
                }
            })
            .then((Response) => {
                const d = Response.data;
                console.log('返回数据', Response.data);
                this.setState({
                    id_senting_work: Response.data.id,
                    // idActive:id,
                });
                if (d.sorry !== undefined || d.error !== undefined) {
                    message.error(d.sorry || d.error);
                } else {
                    // 刷新缓存
                    localStorage.removeItem('my_works');
                    message.success('已成功接单！正在跳转');
                    setTimeout(() => {
                        document.location = `#/about/index/TaskAcepted`
                    }, 1000)
                }
            })
    };
    submitShit = (id) => {
        console.log(id);
        this.setState({
            visibleSubmitShit: true,
        });
    };
    handleOk = () => {
        this.setState({
            visibleSubmitShit: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleSubmitShit: false,
        });
    };
    // 联系方式
    getCommunications = (id) => {
        const ws = this.state.works;
        for (const w in  ws) {
            const p = ws[w];
            console.log(p);
            if (p.id === id) {
                console.log('avl', p);
                return (
                    <div>
                        <h2 className='name'>发布者:{p.communications.name}</h2>
                        <h2 className='name'>QQ:{p.communications.qq}</h2>
                        <h2 className='name'>手机号:{p.communications.phone}</h2>
                        <img src={`${baseUrl}/${p.communications.wx_png}`} alt=''
                             className='avatar'/>
                    </div>
                )
            }
        }
    };
    handleOkCommunications = () => {
        this.setState({
            visibleCommunications: false,
        });
        return 0
    };
    handleCancelCommunications = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    loadMore = () => {
        axios.get(
            baseUrl + '/api/get_works',
            {
                params: {
                    token: localStorage.customer_token,
                    lastWork: this.state.lastProduct !== undefined ? this.state.lastProduct.id : null,            //上拉触底时标记页面底部的任务id
                    // nextWork: this.state.lastProduct,            //下拉刷新时标记页面顶部的任务id
                    order: 'created_at',    //根据什么排序
                    orderValue: this.state.orderValue,          //order要大于还是小于什么
                    esc: 'false',            //正序 or 倒序
                    canEnroll: 'false',
                    work_tpl: this.state.tpl,
                }
            })
            .then((Response) => {
                // 加载结束
                if (Response.data.works.length === 0) {
                    this.setState({hasMoreItems: false});
                    message.success('已全部加载完毕！');
                    return 0
                }
                let products = this.state.works;
                // 去重
                Response.data.works.map(i => {
                    const es = Response.data.hasEnrolled;
                    es.map(e => {
                        if (e.id_work === i.id) {
                            i.enrolled = true;
                        }
                    });
                    // 已报名
                    products.push(i)
                });
                let lastProduct_old = this.state.lastProduct;
                let lastProduct_new = Response.data.works[Response.data.works.length - 1];
                if (lastProduct_old !== lastProduct_new) {
                    this.setState({
                        works: products,
                        // products: products,
                        lastProduct: lastProduct_new,
                        orderValue: Response.data.works[Response.data.works.length - 1].created_at,
                        hasMoreItems: lastProduct_old !== lastProduct_new,
                    })
                }
                console.table('获取之后的state', this.state);
            })
            .catch((err) => {
                console.log(err);
            })
        // this.setState({ hasMoreItems: false })
    };

    generateCells = (ps) => {
        let ls = [];
        ps.map(p =>
            ls.push(
                <div
                    className=
                        {
                            p.stop_at < moment().unix()
                                ? 'cell-work-index cell-disable'
                                : 'cell-work-index'
                        }
                    id={p.id}>
                    <div className='work-cell-row'>
                        {/*标题*/}
                        <span className='work-name'>{p.work_name}</span>
                        {/*报酬*/}
                        <span
                            className='work_money'>报酬:{p.work_money !== 0 ? p.work_money : '可以协商'}{p.work_tpl === 2 || p.work_tpl === 3 ? '积分' : '¥'}</span>
                    </div>
                    <div className='work-cell-row'>
                        <span> </span>
                        <span>需要:{p.work_person_number}人</span>
                    </div>

                    <div className='work-cell-row'>
                        {/*分类*/}
                        <span className='work_tpl'>{this.type_convert(p.work_tpl)}</span>
                        {p.stop_at ?
                            <span>截止时间:{
                                moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?
                                    '今天内'
                                    : moment.unix(p.stop_at).format("MM-DD HH:mm")
                            }

                                </span>
                            : null}
                    </div>
                    <div className='work-cell-row'>
                        {/*头像*/}
                        <Avatar size={32} shape='square' src={`${baseUrl}/${p.senter_img}`}
                                className='avatar'>头像</Avatar>
                        {/*名字*/}
                        <span className='name'>{p.senter_name}</span>
                        {/*报名按钮*/}
                        {
                            //过期判定
                            p.stop_at < moment().unix()
                                ? <Button disabled>已过期</Button>
                                : p.enrolled
                                ? <Button disabled>已接单</Button>
                                : <Button type='primary' onClick={this.working.bind(this, p.id)}>接单</Button>
                        }
                        {/*查看详情*/}
                        <span className='button-more'>
                            <Link to={`/w/${p.id}`}>
                                查看详情
                            </Link>
                            </span>
                    </div>


                    {/*<div className='cell-index' id={p.id}>*/}
                    {/*    /!*头像*!/*/}
                    {/*    <div className='avatar-name'>*/}
                    {/*        <Avatar size={32} shape='square' src={`${baseUrl}/${p.senter_img}`}*/}
                    {/*                className='avatar'>头像</Avatar>*/}
                    {/*        <span className='name'>{p.senter_name}</span>*/}
                    {/*        <div className='money'>*/}
                    {/*            <span*/}
                    {/*                className='work_money'>{p.work_money !== 0 ? p.work_money : '可以协商'}{p.work_tpl=== 2|| p.work_tpl=== 3?'积分':'¥'}/{p.work_person_number}人</span>*/}
                    {/*        </div>*/}
                    {/*        /!*<span className='work_tpl'>{this.type_convert(p.work_tpl)}</span>*!/*/}
                    {/*    </div>*/}
                    {/*    <div className='main-issue'>*/}
                    {/*        <div>*/}

                    {/*            <span className='work_name'>{p.work_name}</span>*/}
                    {/*            /!*<span className='persons'>/{p.work_person_number}人</span>*!/*/}
                    {/*            <div className='descriptions'>*/}
                    {/*                {p.work_content ? <div>{p.work_content}</div> : null}*/}
                    {/*                {p.work_url ? <a href={p.work_url}>相关链接</a> : null}*/}
                    {/*            </div>*/}
                    {/*        </div>*/}
                    {/*        <div>*/}

                    {/*            <div className='time-shit'>*/}

                    {/*                /!*{p.work_time ? <div>发布时间:{moment.unix(p.work_time).format("YYYY-MM-DD HH:mm")}</div>:null}*!/*/}
                    {/*                /!*<br/>*!/*/}
                    {/*                /!*完成任务限定时间:*!/*/}
                    {/*                /!*<br/>*!/*/}
                    {/*                /!*{moment.unix(p.begin_work_time).format("YYYY-MM-DD HH:mm")}~{moment.unix(p.stop_work_time).format("YYYY-MM-DD HH:mm")}*!/*/}
                    {/*                {p.stop_at ?*/}
                    {/*                    <div>截止:{*/}
                    {/*                        moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?*/}
                    {/*                            '今天内'*/}
                    {/*                            : moment.unix(p.stop_at).format("MM-DD HH:mm")*/}
                    {/*                    }*/}

                    {/*                    </div>*/}
                    {/*                    : null}*/}
                    {/*            </div>*/}

                    {/*        </div>*/}
                    {/*    </div>*/}

                    {/*    <div className='btns-shit'>*/}
                    {/*        /!*过期判定*!/*/}
                    {/*        {*/}
                    {/*            p.stop_at<moment().unix()*/}
                    {/*                ?<Button disabled >已过期</Button>*/}
                    {/*                :<Button type='primary' onClick={this.working.bind(this, p.id)}>接单</Button>*/}
                    {/*        }*/}
                    {/*        /!*<Button type='primary' onClick={this.submitShit.bind(this, p.id)}>提交</Button>*!/*/}
                    {/*    </div>*/}
                    {/*</div>*/}
                </div>
            )
        );
        return ls
    };
    type_convert = (type) => {
        switch (type) {
            case 1:
                return "代办类";
            case 2:
                return "转发类";
            case 3:
                return "问卷类";
            case 0:
                return "其他类";
            default:
                return "其他类";
        }
    };

    render() {
        return (
            <div className="Theme">

                <div className='wrapper' ref={(ref) => this.scrollParentRef = ref}>
                    <div className='infinite-div'>
                        <InfiniteScroll
                            pageStart={0}
                            loadMore={this.loadMore}
                            hasMore={this.state.hasMoreItems}
                            loader={<div className="loader" key={0}/>}
                            // useWindow={false}
                            getScrollParent={() => this.scrollParentRef}
                        >
                            {this.generateCells(this.state.works)}
                            {/*联系方式modal*/}
                            {/*<Modal*/}
                            {/*    visible={this.state.visibleCommunications}*/}
                            {/*    onOk={this.handleOkCommunications}*/}
                            {/*    onCancel={this.handleCancelCommunications}*/}
                            {/*    style={{textAlign: 'center'}}*/}
                            {/*>*/}
                            {/*    {this.getCommunications(this.state.idActive)}*/}
                            {/*</Modal>*/}
                            {/*审核modal*/}
                            <Modal
                                visible={this.state.visibleSubmitShit}
                                onOk={this.handleOk}
                                onCancel={this.handleCancel}
                                style={{textAlign: 'center'}}
                            >
                                审核上传表单
                            </Modal>
                        </InfiniteScroll>
                    </div>
                </div>
                {/*<div className='frap'>*/}
                {/*    <Button>*/}
                {/*        发布任务*/}
                {/*    </Button>*/}
                {/*</div>*/}
                <Frap/>
            </div>
        )
    }
}