import React from 'react'
import {Link, Redirect, Route} from 'react-router-dom'
import {Avatar, Button, Icon, Modal} from 'antd';
import './index.less'
import MyFav from "./MyFav";
import TaskAcepted from "./TaskAcepted";
import TaskPosted from "./TaskPosted";
import Frap from "../../../cpns/Frap";
import {Pay,} from "../../../cpns/Pay";
import axios from "axios";
import {baseUrl} from "../../../config";

export default class IndexAbout extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            avatarUrl: '',
            nickName: '',
            token: '',
            // 充值
            visiblePay:false,
        }
    }

    componentDidMount() {
        this.getUserInfo()
        // const cookie2dic = () => {
        //     var cookies = document.cookie.split("; ");
        //     var dic = [];
        //     for (let i = 0; i < cookies.length; i++) {
        //         const e = cookies[i];
        //         var key_shit = e.split("=")[0];
        //         var val = unescape(e.split("=")[1]);
        //         if (key_shit === 'nickName') {
        //             val = decodeURI(e.split("=")[1])
        //         }
        //         dic[key_shit] = val
        //     }
        //     console.log('解析后的cookie:', dic);
        //     return dic
        // };
        // const dic_cookie = cookie2dic();
        // // 设置信息
        // this.setState({
        //     avatarUrl: '' || dic_cookie.avatarUrl,
        //     nickName: '' || dic_cookie.nickName,
        //     token: '' || dic_cookie.token,
        // })
    }

    getUserInfo = () => {
        axios.get(
            baseUrl + '/api/get_user_money',
            {
                params: {
                    token: localStorage.customer_token,
                }
            })
            .then((Response) => {
                console.log(Response);
                this.setState({
                    avatarUrl: Response.data.avatarUrl,
                    nickName: Response.data.nickName,
                    coin: Response.data.coin,
                    money: Response.data.money,
                });
            })
            .catch((err) => {
                console.log(err);
            })
    };
    showPay = () => {
        this.setState({
            visiblePay: true,
        });
    };
    handleOk = () => {
        this.setState({
            visiblePay: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visiblePay: false,
        });
    };
    render() {
        return (
            <div className="About">
                {/* 用户名与头像 */}
                <div className='header'>
                    <Avatar size={64} src={`${baseUrl}/${this.state.avatarUrl}`}/>
                    <div className='user-name'>
                        <h3>
                        {this.state.nickName}
                        </h3>
                    </div>
                    {/*<Link to='/about/communications'>设置</Link>*/}
                </div>
                {
                    this.state.money!==undefined?
                <div className='user-info'>
                    <div className='coin'>
                        {this.state.money}积分
                    <br/>
                    <a onClick={this.showPay}>充值</a>
                    </div>
                </div>
                    :null
                }
                {/* 子路由导航 */}
                <div className='nav-route'>

                    <Link
                        to={`${this.props.match.url}/TaskPosted`}
                        className={document.location.hash.includes('TaskPosted') ? 'active' : 'unactive'}
                    >
                        <Icon type="carry-out" style={{fontSize: '30px'}}/>
                        <div>
                            我的发布
                        </div>
                    </Link>
                    <Link
                        to={`${this.props.match.url}/TaskAcepted`}
                        className={document.location.hash.includes('TaskAcepted') ? 'active' : 'unactive'}
                    >
                        <Icon type="container" style={{fontSize: '30px'}}/>
                        <div>
                            我的接单
                        </div>

                    </Link>
                    <Link
                        to={`${this.props.match.url}/MyFav`}
                        className={document.location.hash.includes('MyFav') ? 'active' : 'unactive'}
                    >
                        <Icon type="star" style={{fontSize: '30px'}}/>
                        <div>
                            我的收藏
                        </div>

                    </Link>
                </div>
                {/* 子路由 */}
                <div className='routes-container'>
                    <Route exact path={this.props.match.url} render={() => (
                        <Redirect to={`${this.props.match.url}/TaskAcepted`}/>
                    )}/>
                    <Route exact path={`${this.props.match.url}/MyFav`} component={MyFav}/>
                    <Route exact path={`${this.props.match.url}/TaskPosted`} component={TaskPosted}/>
                    <Route  path={`${this.props.match.url}/TaskAcepted`} component={TaskAcepted}/>
                </div>
                <Modal
                    visible={this.state.visiblePay}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    style={{textAlign: 'center'}}
                >
                    <Pay/>
                </Modal>
                <Frap/>
            </div>

        )
    }
}