import { configure,observable, computed,action } from 'mobx';

configure({
    enforceActions: 'observed'
});

class Store {
    @observable name = 'Bartek';
    @computed get decorated() {
        return `${this.name} is awesome!`;
    }
    @action changeName = (v)=>{
        this.name = v;
    }
}

export default new Store();