import React from 'react'
import './index.less'
import {Form, Icon, Input, message, notification} from 'antd';
import axios from "axios";
import {baseUrl} from "../../config";
import wx from 'weixin-js-sdk';

const FormItem = Form.Item;

class PayForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // 防止重复点击
            isClick: true,
        }
    }

    handleSubmit = () => {
        const isClick = this.state.isClick;
        console.log('点了！！！！');
        if (isClick) {
            // 获取所有的值
            let allVals = this.props.form.getFieldsValue();
            console.log(allVals);
            this.props.form.validateFields((err, values) => {
                if (!err) {
                    this.setState({isClick: false});
                    console.log('表单值', values);
                    // 发送
                    axios.get(
                        baseUrl + '/api/before_pay',
                        {
                            params: {
                                'total_fee': values.total_fee,
                                'token': localStorage.customer_token,//*
                            }
                        })
                        .then((Response) => {
                            console.log(Response.data);
                            const d = Response.data;
                            if (d.success) {
                                const s = d.success;
                                console.log('paying');
                                if (typeof WeixinJSBridge == "undefined"){
                                    if( document.addEventListener ){
                                        document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                                    }else if (document.attachEvent){
                                        document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
                                        document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                                    }
                                }else{
                                    onBridgeReady();
                                }
                                function onBridgeReady(){
                                    let WeixinJSBridge = window.WeixinJSBridge;
                                    WeixinJSBridge.invoke(
                                        'getBrandWCPayRequest', {
                                            "appId":"wx5e44595fadf1d2a2",     //公众号名称，由商户传入
                                            "timeStamp":s.timeStamp,         //时间戳，自1970年以来的秒数
                                            "nonceStr":s.nonceStr, //随机串
                                            "package":s.package,
                                            "signType":'md5',         //微信签名方式：
                                            "paySign":s.paySign,
                                        },
                                        function(res){
                                            if(res.err_msg == "get_brand_wcpay_request:ok" ){
                                                // 使用以上方式判断前端返回,微信团队郑重提示：
                                                //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
                                                notification.open({
                                                    message: '成功',
                                                    placement: 'bottomLeft',
                                                    icon: <Icon type="smile" style={{color: '#108ee9'}}/>,
                                                });
                                                document.location = `#/about`
                                            }else {

                                                alert(`
                                                参数：${
                                                    JSON.stringify(
                                                    {
                                                        "appId":"wx5e44595fadf1d2a2",     //公众号名称，由商户传入
                                                    "timeStamp":s.timeStamp,         //时间戳，自1970年以来的秒数
                                                    "nonceStr":s.nonceStr, //随机串
                                                    "package":s.package,
                                                    "signType":'MD5',         //微信签名方式：
                                                    "paySign":s.paySign,
                                                    })
                                                    }
                                                错误原因:
                                                ${JSON.stringify(res)}
                                                `)
                                            }
                                        });
                                }


                            } else {
                                message.error('充值失败，请稍后重试', d.sorry || d.error);
                                this.setState({isClick: true});
                            }
                        })
                }
            });
        }

    };

    render() {
        const {getFieldDecorator, getFieldValue} = this.props.form;
        return (
            <div>
                <h3>充值</h3>
                <Form>
                    <FormItem label='充值数目'>
                        {getFieldDecorator('total_fee', {
                            initialValue: 0,
                            rules: [{
                                pattern: /^(0|[1-9][0-9]*)$/, message: '只能大于等于0'
                            }],
                        })(
                            <div>
                                <Input placeholder='分' className='pay'/>
                            </div>
                        )
                        }
                    </FormItem>
                    <FormItem>
                        <div className='submit-footer-small'>
                            <div className='submit-footer-cell' onClick={this.handleSubmit}>
                                {this.state.isClick ? '充值' : '充值中...'}
                            </div>
                        </div>
                    </FormItem>
                </Form>
            </div>
        )
    }
}

export const Pay = Form.create()(PayForm);
