import React from 'react'
import './index.less'
import {Avatar, Button, Modal, Spin} from 'antd';
import {baseUrl} from "../../../../../config";
import moment from 'moment'
import {UploadWorkDone} from "./UploadWorkDone";
import {Link, Redirect, Route} from "react-router-dom";
import store from './store'
import {observer} from "mobx-react";

@observer
class TaskAceptedCell extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myFavs: [],
            contentModal: '',
            visibleCommunications: false,
            worksIng: [],
            worksExpired: [],
        };
        // store state初始化
        store.fetchWorks();
        // 设置筛选类型
        store.setTPL(this.props.tpl);
        // 设置排序类型
        store.setSort(this.props.sort);
    }
    renderMyWorks = ()=>{
        const ws = store.worksByTpl;
        const cpns = [];
        for(const key in ws){
            const p = ws[key];
            cpns.push(
                <div
                    className=
                        {
                            p.stop_at < moment().unix()
                                ? 'cell-work-index cell-disable'
                                : 'cell-work-index'
                        }
                    id={p.id}>
                    <div className='work-cell-row'>
                        {/*标题*/}
                        <span className='work-name'>{p.work_name}</span>
                        {/*报酬*/}
                        <span
                            className='work_money'>报酬:{p.work_money !== 0 ? p.work_money : '可以协商'}{p.work_tpl === 2 || p.work_tpl === 3 ? '积分' : '¥'}</span>
                    </div>
                    <div className='work-cell-row'>
                        <span> </span>
                        <span>需要:{p.work_person_number}人</span>
                    </div>

                    <div className='work-cell-row'>
                        {/*分类*/}
                        <span className='work_tpl'>{this.type_convert(p.work_tpl)}</span>
                        {p.stop_at ?
                            <span>截止时间:{
                                moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?
                                    '今天内'
                                    : moment.unix(p.stop_at).format("MM-DD HH:mm")
                            }

                                </span>
                            : null}
                    </div>
                    <div className='work-cell-row'>
                        {/*头像*/}
                        <span>

                        <Avatar size={32} shape='square' src={`${baseUrl}/${p.senter_img}`}
                                className='avatar'>头像</Avatar>
                            {/*名字*/}
                            <span className='name'>{p.senter_name}</span>
                        </span>
                        {/*报名按钮*/}
                        {
                            //过期判定
                            p.stop_at < moment().unix()
                                ? <Button disabled>已过期</Button>
                                : <div>
                                    <Button
                                        className='button-margin'
                                        type='primary'
                                        onClick={this.showCommunications.bind(this, p.id)}>联系</Button>
                                    <Button
                                        className='button-margin'
                                        type='primary'
                                        onClick={this.uploadWork.bind(this, p.id)}>提交审核</Button>
                                </div>
                        }
                        {/*查看详情*/}
                        <span className='button-more'>
                            <Link to={`/w/${p.id}`}>
                                <Button
                                >查看详情</Button>

                                </Link>
                            </span>
                    </div>
                </div>
            )
        }
        return cpns
    };
    getCommunications = (id) => {
        const ws = store.worksByTpl;
        for (const w in  ws) {
            const p = ws[w];
            // console.log(p);
            if (p.id === id) {
                return (
                    <div>
                        <h2 className='phone'>手机号:{p.communications.phone}</h2>
                        <img src={`${baseUrl}/${p.communications.wx_png}`} alt=''
                             className='avatar'
                             width='100%'
                        />
                        <h5>提示：长按识别二维码添加微信</h5>
                    </div>
                )
            }
        }
    };
    showCommunications = (id) => {
        console.log(id);
        this.setState({
            visibleCommunications: true,
            contentModal: this.getCommunications(id),
        });
    };
    uploadWork = (id) => {
        console.log(id);
        localStorage.id_work = id;
        this.setState({
            visibleCommunications: true,
            contentModal: <UploadWorkDone/>,
        });
    };
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    type_convert = (type) => {
        switch (type) {
            case 1:
                return "代办类";
            case 2:
                return "转发类";
            case 3:
                return "问卷类";
            case 0:
                return "其他";
            default:
                return "全部";
        }
    };
    render() {
        return (
                <div className='my-works-accepted'>
                    {this.renderMyWorks()}
                    {/*联系跟上传*/}
                    <Modal
                        visible={this.state.visibleCommunications}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        style={{textAlign: 'center'}}
                    >
                        {this.state.contentModal}
                    </Modal>
                </div>
        )

    }
}
export default TaskAceptedCell;