import {action, computed, configure, observable} from 'mobx';
import axios from 'axios'
import {baseUrl} from "../../../../../config";
import moment from "moment";

configure({
    enforceActions: 'observed'
});

class StoreWorkAcepted {
    @observable works = [];
    @observable filterTpl = 0;
    @observable sort = '';

    @computed get workers() {
        return this.works.workers ? this.works.workers : [];

    }

    @computed get worksByTpl() {
        const ws = this.works;
        const tpl = this.filterTpl;
        // 筛选
        let filleted = [];
        for(const key in ws){
            const w = ws[key];
            if (w.work_tpl==tpl||tpl==undefined){
                filleted.push(w);
            }
        }
        // 排序
        if (this.sort=='done') {
            // 排序最近
            let ws = filleted.sort((a,b)=>{
                const now = moment().unix();
                return Math.abs(now-a.stop_at)-Math.abs(now-b.stop_at)
            });
            const now = moment().unix();
            const arrNot = ws.filter((w ) => w.stop_at <now);
            const arrYet = ws.filter((w) => w.stop_at >now);
            arrYet.push(...arrNot);
            return arrYet
        }

        return filleted.reverse()
    }
    @computed get Communications() {
        const ws = this.works;
        let cmm = [];
        for(const key in ws) {
            const w = ws[key];
            cmm.push(w.communications)
        }
        return cmm
    }

    @computed get workeFile() {
        return this.works.work_file ? this.works.work_file : [];
    }

    // 动作
    @action setWorks = (v) => {
        this.works = v;
    };
    @action setTPL = (v) => {
        this.filterTpl = v;
    };
    @action setSort = (v) => {
            this.sort = v;
        };

    @action
    async fetchWorks(id) {
        const cached = localStorage.my_works !== undefined;
        if (cached) {
            // 返回数据
            this.setWorks(JSON.parse(localStorage.my_works));
        } else {
            //没有缓存就请求
            await axios.get(
                baseUrl + '/api/get_my_works',
                {
                    params: {
                        token: localStorage.customer_token,
                        type: 'all',
                    }
                })
                .then((r) => {
                    console.log("in axios ", r.data);
                    this.setWorks(r.data.works);
                    // 设置缓存
                    localStorage.my_works = JSON.stringify(r.data.works)
                })
                .catch((err) => {
                    console.log("in axios ", err)
                });
        }
    }

}

export default new StoreWorkAcepted();