import React from 'react'
import './index.less'
import axios from "axios";
import {Link} from "react-router-dom";
import {baseUrl} from "../../../../config";
import {Spin} from "antd";

export default class MyFav extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myFavs: [],
        }
    }

    componentWillMount() {
        console.log(this.state);
        this.getFav()
    }

    getFav = (id) => {
        // 有无缓存？返回：请求->缓存->返回
        const cached = localStorage.collected_product!==undefined;
        if(cached){
            // 返回数据
            this.setState({
                myFavs:JSON.parse(localStorage.collected_product),
            })
        }else {
            // 请求
            axios.get(
                baseUrl + '/api/get_collected_product',
                {
                    params: {
                        token: localStorage.customer_token,
                    }
                })
                .then((Response) => {
                    console.log('请求了works_by_me')
                    this.setState({
                        myFavs: Response.data.products,
                    });
                    // 设置缓存
                    localStorage.collected_product = JSON.stringify(Response.data.products)
                });
        }
    };
    renderFav = () => {
        if (this.state.myFavs.length === 0) {
            return <h2>暂无收藏</h2>
        }
        let fs = [];
        this.state.myFavs.map(f =>
            fs.push(
                <li className='fav-cell'>
                    <Link to={`/seller/${f.id_seller}/p/${f.id}`}>
                        <img src={`${baseUrl}${f.productDatas[0]}`} alt=''/>
                        <h2>{f.product_name}</h2>
                        <span className='price'>¥{f.price}</span>
                    </Link>
                </li>
            )
        );
        return fs
    };

    render() {
        return (
            this.state.myFavs !== undefined
                ?
                <div className='my-favs'>
                    {this.renderFav()}
                </div>
                : <Spin/>

        )

    }
}
