import React from 'react'
import {Route} from 'react-router-dom'
import Index from './index/index';
import Seller from './seller';
import Product from './seller/goods/product/index';

export default class SellerIndex extends React.Component {
    render() {
        return (
            <div className="Seller">
                <div className="route-content">
                    <Route path="/seller" component={Index} exact/>
                    <Route path='/seller/:id' component={Seller}/>
                    <Route path='/seller/:id/p/:pid' component={Product} exact/>
                </div>
            </div>

        )
    }
}