import React from 'react'
import './index.less'
import {Avatar, Button, Modal, Spin} from 'antd';
import axios from "axios";
import {baseUrl} from "../../../../config";
import moment from 'moment'
import {Link} from "react-router-dom";

export default class TaskPosted extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myFavs: [],
            idActive: 0,
            visibleCommunications: false,
        }
    }

    componentWillMount() {
        console.log(this.state);
        this.getFav()
    }

    getCommunications = (id) => {
        const ws = this.state.myFavs;
        for (const w in  ws) {
            const p = ws[w];
            console.log(p);
            if (p.id === id) {
                // console.log('avl', p);
                return (
                    <div>
                        <h2 className='name'>手机号:{p.communications.phone}</h2>
                        <img src={`${baseUrl}/${p.communications.wx_png}`} alt=''
                             className='avatar'
                             width='100%'
                        />
                        <h5>提示：长按识别二维码添加微信</h5>
                    </div>
                )
            }
        }
    };
    getFav = () => {
        // 有无缓存？返回：请求->缓存->返回
        // const cached = localStorage.works_by_me!==undefined;
        const cached = false;

        if (cached) {
            // 返回数据
            this.setState({
                myFavs: JSON.parse(localStorage.works_by_me),
            })
        } else {
            // 请求
            axios.get(
                baseUrl + '/api/get_works_by_me',
                {
                    params: {
                        token: localStorage.customer_token,
                    }
                })
                .then((Response) => {
                    console.log('请求了works_by_me');
                    this.setState({
                        myFavs: Response.data.works,
                    });
                    // 设置缓存
                    localStorage.works_by_me = JSON.stringify(Response.data.works)
                });
        }
    };
    showCommunications = (id) => {
        console.log(id);
        this.setState({
            visibleCommunications: true,
            idActive: id,
        });
    };
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    type_convert = (type) => {
        switch (type) {
            case 1:
                return "代办类";
            case 2:
                return "转发类";
            case 3:
                return "问卷类";
            case 0:
                return "其他";
            default:
                return "全部";
        }
    };
    renderFav = () => {
        if (this.state.myFavs.length === 0) {
            return <h2>暂无</h2>
        }
        let fs = [];
        const ws = this.state.myFavs;
        for (const w in  ws) {
            const p = ws[w];
            fs.push(
                <div
                    className=
                        {
                            p.stop_at < moment().unix()
                                ? 'cell-work-index cell-disable'
                                : 'cell-work-index'
                        }
                    id={p.id}>
                    <div className='work-cell-row'>
                        {/*标题*/}
                        <span className='work-name'>{p.work_name}</span>
                        {/*报酬*/}
                        <span
                            className='work_money'>报酬:{p.work_money !== 0 ? p.work_money : '可以协商'}{p.work_tpl === 2 || p.work_tpl === 3 ? '积分' : '¥'}</span>
                    </div>
                    <div className='work-cell-row'>
                        <span> </span>
                        <span>需要:{p.work_person_number}人</span>
                    </div>

                    <div className='work-cell-row'>
                        {/*分类*/}
                        <span className='work_tpl'>{this.type_convert(p.work_tpl)}</span>
                        {p.stop_at ?
                            <span>截止时间:{
                                moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?
                                    '今天内'
                                    : moment.unix(p.stop_at).format("MM-DD HH:mm")
                            }

                                </span>
                            : null}
                    </div>
                    <div className='work-cell-row'>
                        {/*头像*/}
                        <span>

                        <Avatar size={32} shape='square' src={`${baseUrl}/${p.senter_img}`}
                                className='avatar'>头像</Avatar>
                        {/*名字*/}
                        <span className='name'>{p.senter_name}</span>
                        </span>
                        {/*报名按钮*/}
                        {
                            //过期判定
                            p.stop_at < moment().unix()
                                ? <Button disabled>已过期</Button>
                                : null
                        }
                        {/*查看详情*/}
                        <span className='button-more'>
                            <Link to={`/work/${p.id}`}>
                                    查看详情
                                </Link>
                            </span>
                    </div>
                </div>
            )
        }

        return fs
    };

    render() {
        return (
            this.state.myFavs !== undefined
                ?
                <div className='my-favs'>
                    {this.renderFav()}
                    <Modal
                        visible={this.state.visibleCommunications}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        style={{textAlign: 'center'}}
                    >
                        {this.getCommunications(this.state.idActive)}
                    </Modal>
                </div>
                : <Spin/>

        )

    }
}
