import React from 'react'
import './index.less'
import {Button, Icon, message, Modal, notification, Spin} from 'antd';
import axios from 'axios'
import {CopyToClipboard} from 'react-copy-to-clipboard';
import InfiniteScroll from 'react-infinite-scroller';
import {baseUrl} from '../../config';
import html2canvas from 'html2canvas';
import {Link} from 'react-router-dom';

const QRCode = require('qrcode.react');

message.config({
    top: 500,
    duration: 2,
    maxCount: 2,
});
export default class Goods extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            products: [],
            hasMoreItems: true,
            visible: false,
            cache: '',
            canvas: '',
            imgUrl: '',
            finalImgUrl: '',
            filled:[],
        }
    }

    componentDidMount() {
        console.log('ddd', this.props);

    }

    delete = (id) => {
        axios.get(
            baseUrl + '/api/delete_row',
            {
                params: {
                    token: localStorage.token, //用户token
                    type: 'product',  //货号
                    id_inf: id, //描述
                }
            })
            .then((Response) => {
                notification.open({
                    message: '删除成功',
                    description: '正在刷新...',
                    placement: 'bottomLeft',
                    icon: <Icon type="smile" style={{color: '#108ee9'}}/>,
                });
                setTimeout(() => {
                    document.location.reload()
                }, 500);
            })
    };
    favoriteHandler = (id) => {
        axios.get(
            baseUrl + '/api/collect_product',
            {
                params: {
                    token: localStorage.customer_token, //用户token
                    id_product: id,  //货号
                }
            })
            .then((Response) => {
                console.log('收藏后', Response);
                if (Response.data === 'success') {
                    message.success('收藏成功');
                    let filledOld = this.state.filled;
                    filledOld.push(id);
                    this.setState({
                        filled:filledOld,
                    })
                    console.log('点击后',this.state)
                } else {
                    message.success('收藏失败，请稍后重试');
                }
            })
    };

    getProducts = (ps) => {
        let ls = [];
        ps.map(p =>
            ls.push(
                <li className="vproductShow-card-cell">
                    <div id={p.id}>
                        <img src={`${baseUrl}${p.productDatas[0]}`} alt=''/>
                        <div className='goods-bottom'>
                            {/* 收藏按钮 */}

                            <div className='icon-favorite' onClick={this.favoriteHandler.bind(this, p.id)}>
                                <Icon type="heart"  theme={this.state.filled.indexOf(p.id)!==-1?'filled':''} style={{fontSize: '1.5em', color: 'pink'}}/>
                            </div>
                            {/* 商品描述 */}
                            <div className='description-goods'>
                                <h2>{p.product_name}</h2>
                                <p className='infs'>{p.infs}</p>
                                <div className='price-and-pop'>
                                    <span className='price'>¥{p.price}</span>
                                    <span className='pop'>爆款</span>
                                    <span>  </span>
                                    <Link to={`/seller/${this.props.id}/p/${p.id}`}>
                                        <Icon type="arrow-right" style={{fontSize: '18px', color: 'gray'}}/>
                                    </Link>
                                    {/* <span className='pop'>爆款</span> */}
                                    {/* <span className='pop'>{p.infs}</span> */}
                                </div>
                            </div>
                            {/* 分享按钮 */}
                            <div className='icon-share'>
                                <Icon type="share-alt" style={{fontSize: '1.5em', color: 'gray'}}/>
                            </div>
                        </div>
                        {this.props.login ?
                            <button onClick={this.delete.bind(this, p.id)}>删除<Icon type="delete"/></button>
                            : null
                        }
                    </div>
                    {/* <div className='buttons-group'>
                        {this.renderButtonShit(p.productUrl)}
                        <button className='button-share' onClick={this.share.bind(this, p.id)}>分享<Icon type="share-alt" /></button>
                    </div> */}
                </li>
            )
        );
        return ls
    };
    renderButtonShit = (us) => {
        us.map(u => {
            let ls = [];
            if (u.content_type === "taok") {
                ls.push(
                    < CopyToClipboard text={u.content}
                                      onCopy={this.onCopy}>
                        <Button className='button-copy' onClick={this.infoCopied}>
                            <Icon type="copy"/>淘口令
                        </Button>
                    </CopyToClipboard>
                )
            } else {
                ls.push(
                    <a href={u.content.includes('http://') || u.content.includes('https://') ? u.content : `https://${u.content}`}>
                        <Button className='button-copy'>
                            <Icon type="link"/>{this.type_convert(u.content_type)}
                        </Button>
                    </a>
                )
            }
            return ls
        })
    };
    share = (id) => {
        let good_shit = this.state.products.map(p => {
            if (p.id === id) {
                return <div>
                    <div className='cache-goods'>
                        <img src={`${baseUrl}${p.productDatas[0]}`} alt=''></img>
                        <div className='price-and-pop'>
                            <span className='price'>¥{p.price}</span>
                            <span className='pop'>爆款</span>
                            {/* <span className='pop'>{p.infs}</span> */}
                        </div>
                        <h2>{p.product_name}</h2>
                        <QRCode value={window.location.href} size={80}/>
                        <div className='words'>
                            <h2>小蓝色本有好东西</h2>
                            <h4>扫描二维码查看商品</h4>
                            <h4>移动端长按保存图片</h4>
                        </div>
                    </div>
                </div>
            }
            return 0;
        });
        this.setState({
            cache: good_shit,
            visible: true
        });

        let cache = document.querySelector('.goods');
        console.log(cache);
        html2canvas(cache, {
            height: 300,
            width: 300,
            useCORS: true
        }).then(async canvas => {
            console.log(canvas);
            // 转图片
            var canvasUrl = canvas.toDataURL();
            console.log(canvasUrl);
            var myImage = canvas.toDataURL("image/png");
            this.setState({finalImgUrl: myImage})
        })
            .then(canvas => {
                // that.browserRedirect();
            });
        return 0;
    };
    canvas_shit = (id) => {
        console.log(id, '节点>>>', document.getElementById(id));
        let html_shit = document.getElementById(id);
        html2canvas(html_shit, {
                taintTest: false,
                useCORS: true,
                allowTaint: false,
                backgroundColor: null,
                logging: true,
                onrendered: function (canvas) {
                    // imgBlob = canvas.toDataURL('image/jpeg', 1.0); //将图片转为base64, 0-1 表示清晰度
                    // imgBlob = imgBlob.toString().substring(imgBlob.indexOf(",") + 1);//截取base64以便上传
                }
            }
        ).then(canvas => {
                console.log(canvas);
                var imgUrl = canvas.toDataURL("image/png", 1); // 此方法可以设置截图质量（0-1）
                console.log("base64编码数据：", imgUrl);
                this.setState({imgUrl: imgUrl});
                console.log(this.state.canvas);
                document.querySelector('body').appendChild(canvas)
            }
        )
    };
    infoCopied = () => {
        message.success('已复制到剪贴板，快去安排吧～');
    };
    handleOk = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };
    type_convert = (type) => {
        switch (type) {
            case "taok":
                return "淘口令";
            case "pdd":
                return "拼多多";
            case "tburl":
                return "淘宝链接";
            case "jd":
                return "京东链接";
            default:
                return "淘宝链接";
        }
    };
    loadMore = () => {
        axios.get(
            baseUrl + '/api/get_products',
            {
                params: {
                    id_seller: this.props.id,
                    order: 'id',
                    last_product: this.state.lastProduct,
                    order_value: this.state.lastProduct,
                    esc: 'true'
                }
            })
            .then((Response) => {
                // 加载结束
                if (Response.data.products.length === 0) {
                    this.setState({hasMoreItems: false});
                    message.success('已全部加载完毕！');
                    return 0
                }
                let products = this.state.products;
                // 去重
                Response.data.products.map(i => {
                    let inProducts = false;
                    this.state.products.map(p => {
                        if (p.id === i.id) {
                            inProducts = true
                        }
                        return 0 ;
                    });
                    if (!inProducts) {
                        products.push(i)
                    }
                    return 0 ;
                });
                let lastProduct_old = this.state.lastProduct;
                let lastProduct_new = Response.data.products[Response.data.products.length - 1].id;
                if (lastProduct_old !== lastProduct_new) {
                    this.setState({
                        products: products,
                        lastProduct: lastProduct_new,
                        hasMoreItems: lastProduct_old !== lastProduct_new,
                    })
                }
                console.table('state', this.state);
            })
            .catch((err) => {
                console.log(err);
            })
        // this.setState({ hasMoreItems: false })
    };

    render() {
        return (
            <div className='goods'>
                <ul className="vproductShow-card-container">
                    <div className='wrapper' ref={(ref) => this.scrollParentRef = ref}>
                        <div className='infinite-div'>
                            <InfiniteScroll
                                pageStart={0}
                                loadMore={this.loadMore}
                                hasMore={this.state.hasMoreItems}
                                loader={<div className="loader" key={0}><Spin/></div>}
                                // useWindow={false}
                                getScrollParent={() => this.scrollParentRef}
                            >
                                {this.getProducts(this.state.products)}
                                {/* 分享缓冲 */}
                                <div className='cache' ref='cache'>
                                    {this.state.cache}
                                </div>
                            </InfiniteScroll>
                        </div>
                    </div>

                </ul>
                <Modal
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <div className='for-cavans'>
                        <img alt='' src={this.state.finalImgUrl}/>
                    </div>
                </Modal>
                <div id="canvas_handler" title='e'/>
            </div>
        )

    }
}
